body {
  text-align: center;
}

input {
  width: 500px;
  height: 40px;
  background-color: ffffff;
  border: 1px solid #ccc;
  padding: 0 10px;
}

button {
  width: 500px;
  height: 40px;
  cursor: pointer;
  background-color: #3f7fec;
  border: none;
  margin-bottom: 50px;
}

button:disabled {
  background-color: #db4141;
}

button:hover, button:focus {
  background-color: #ecf8c3;
  border: 2px solid #ecf8c3;
}

.timer {
  justify-content: center;
  display: flex;
}

.field {
  width: 80px;
  border: 1px solid #ccc;
  flex-direction: column;
  align-items: center;
  margin-right: 20px;
  padding: 10px 0;
  display: flex;
}

.field:last-child {
  margin-right: 0;
}

.value {
  font-size: 40px;
  font-weight: 400;
}

.label {
  color: #ccc;
}

